
.yps-1-1{
  background-color: #2c72a167;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding: 64px 0px 16px 0px;
  position: relative;
  z-index: 1;
 margin: 0px 0px 40px 0px;
}


.yps-1-1::before{
  content: '';
  position: absolute;
  top: 140px;
  background-color: #182126;
  width: 600px;
  height: 186px;
  padding: 32px;
  border-radius: 32px 32px 0px 0px;
  z-index: -1;
}

.yps-1-1>div>div>h3{
  font-size: 32px;
  font-weight: bold;
  margin: 8px;
}

.yps-1-1>div>div{
  display: flex;
  justify-content: center;
  align-items: center;
}
 
.yps-1-1>div>div>p{
   margin: 16px;
   font-size: 13px;
   color: rgba(255, 255, 255, 0.547);
}


.profile_pic_container>img{
    width: 160px;
    height: 160px;
    object-fit: cover;
    border-radius: 50%;
}


.yps-1-2{
  width: 60%;
  display: block;
  columns: 16rem;
  gap: 1rem;
  margin: 24px auto;
}

.yps-1-2-1{
  display: flex;
  box-sizing: border-box;
  position: relative;
  margin-bottom: 1rem;
  break-inside: avoid;
  border: 4px solid #2c72a100;
  border-radius: 9px;
  transition: all 0.3s ease;
  text-decoration: none;
}



.yps-1-2-1>img {
  width: 100%;
  display: block;
  border-radius: 8px;
}
.post_options {
  box-sizing: border-box;
  position: absolute;
  bottom: -4px;
  width: 100%;
  background-color: #2C72A1;
  padding: 8px 16px;
  border-radius: 0px 0px 9px 9px;
  display: flex;
  color: white;
  display: none;
  align-items: center;
}

.post_options>div>p {
  margin: 0px;
  padding: 0px;
  font-size: 10px;
}

.post_options>div {
  margin: 0px 8px 0px 0px;
}

.post_options>svg:last-child {
  position: absolute ;
  right: 16px;
}

.yps-1-2-1:hover {
  margin-bottom: 1rem;
  break-inside: avoid;
  border: 4px solid #2C72A1;
  border-radius: 9px;
}

.yps-1-2-1:hover>div {
  display: flex;
}

.btn-3{
  background-color: #2C72A1;
  border: none;
  outline: none;
  padding: 8px 16px;
  border-radius: 16px;
  color: white;
  letter-spacing: 1px;
  font-weight: bold;
  margin: 0px;
}

.profile_pic_container{
  position: relative;
}

.profile_name_container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile_pic_edit{
  position: absolute;
  text-decoration: none;
  right: 0px;
  bottom: 16px;
}

.profile_pic_edit>input{
  position: absolute;
  opacity: 0;
  width: 30px;

}



@media only screen and (max-width:1000px) {
        
      .yps-1-1::before{
        width: 70%;
      }

      .yps-1-2{
        width: 90%;
        display: block;
        columns: 12rem;
        gap: 1rem;
        margin: 16px auto;
      }
}