main {
    display: flex;
}

.yhs-2 {
    padding: 64px 32px;
    border-right: 1px solid #2c72a161;
}

.profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    margin: 0px 0px 20px 0px;
    text-decoration: none;
}

.profile>p {
    margin: 0px;
}

.profile>p>svg{
    margin: 0px 0px 0px 8px;
}

.profile>img{
    width: 90px; 
    height: 90px;
    object-fit: cover;
    border-radius: 50%;
}


.yhs-2-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px 32px;
}

.yhs-2-1 {
    background-color: white;
    border-radius: 50px;
}

 
.yhs-2-1>input {
    /* padding: 2px 16px; */
    background-color: #2c72a100;
    border: none;
    /* border-bottom: 1px solid white; */
    margin: 0px 0px 0px 16px;
    outline: none;
    color: #2C72A1;
}

::placeholder {
    color: white;
    color: #2C72A1;
}


.yhs-2-2 {
    margin: 16px 0px; 
    color: white;
}

.yhs-2-2-1 {
    margin: 16px 0px;
    padding: 0px 0px 16px 0px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.212);
}

.yhs-2-2-1:last-child {
    border-bottom: none;
}

.yhs-2-2-1>h3 {
    padding: 0px;
    margin: 0px;
}

.yhs-2-2-1-1 {
    display: flex;
    flex-direction: column;
}

.yhs-2-2-1-1>a {
    color: #2C72A1;
    opacity: 0.6;
    text-decoration: none;
}



.yhs-1 {
    width: 100%;
    display: block;
    columns: 16rem;
    gap: 1rem;
    margin: 16px 120px;
}

.yhs-1-1 {
    display: flex;
    box-sizing: border-box;
    position: relative;
    margin-bottom: 1rem;
    break-inside: avoid;
    border: 4px solid #2c72a100;
    border-radius: 9px;
    transition: all 0.3s ease;
    text-decoration: none;
}



.yhs-1-1>img {
    width: 100%;
    display: block;
    border-radius: 8px;
}

.yhs-1-1>div:nth-child(1) {
    position: absolute;
    color: white;
    background-color: #2C72A1;
    padding: 6px 16px;
    margin: 16px 0px;
    border-radius: 0px 9px 9px 0px;
    display: none;
}

.post_options {
    box-sizing: border-box;
    position: absolute;
    bottom: -4px;
    width: 100%;
    background-color: #2C72A1;
    padding: 8px 16px;
    border-radius: 0px 0px 9px 9px;
    display: flex;
    color: white;
    display: none;
}

.post_options>div>p {
    margin: 0px;
    padding: 0px;
    font-size: 10px;
}

.post_options>div {
    margin: 0px 8px 0px 0px;
}

.yhs-1-1:hover {
    margin-bottom: 1rem;
    break-inside: avoid;
    border: 4px solid #2C72A1;
    border-radius: 9px;
}

.yhs-1-1:hover>div {
    display: flex;
}

.create-btn {
    position: fixed;
    right: 40px;
    bottom: 40px;
}


.yhs-2-3{
    text-align: center;
}

.yhs-2-3>a{
    color: white;
}


.myfollowing{
    text-align: center;
    color: white;
}
 
.myfollowing>a {
    font-size: 13px;
    /* text-decoration: none; */
    color: rgb(149, 149, 149);
    display: inline-block;
    margin: 0px 0px 24px 0px;
}


.user_container{
    padding: 16px;
    display: flex;
    flex-direction: column;
}

.user_container>a{
    margin: 0px 0px 4px 0px;
    color: white;
}



@media only screen and (max-width:1400px) {
    .profileHeader {
        display: flex;
    }

    .yhs-2 {
        display: none;
    }
}

@media only screen and (max-width:1100px) {
    .yhs-1 {
        margin: 16px 128px;
        padding: 64px 0px 0px 0px;
    }
}

@media only screen and (max-width:600px) {
    .yhs-1 {
        margin: 16px 32px;
    }
}