.ysps-1 {
    padding: 64px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
}

.ysps-1-1 {
    display: flex;
}

.ysps-1-1>img {
    border-radius: 16px;
    width: 400px;
    margin: 0px 32px 0px 0px;
    height: auto;
    object-fit: cover;
}

.ysps-1-1-2 {
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.242);
    padding: 16px;
    width: 460px;
    height: auto;
    /* overflow-y: scroll; */
    position: relative;
}


.profile-sp {
    display: flex;
    text-decoration: none;
    color: white;
}

.profile-sp>p {
    color: #2C72A1;
    ;
    margin: 0px 0px 0px 8px;
}

.comment {
    margin: 0px 0px 24px 32px;
    padding: 0px 0px 16px 0px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.623);
}

.ysps-1-1-2>div:nth-child(1) {
    height: 460px;
    overflow-y: scroll;
}

.ysps-1-1-2>div:nth-child(1)::-webkit-scrollbar {
    width: 0px;
}

.ysps-1-1-2-2>div {
    display: flex; 
    align-items: center;
}

.ysps-1-1-2-2>div>div {
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0px 16px 0px 0px;
} 

.ysps-1-1-2-2>div>div>p {
    margin: 0px 0px 0px 8px;
    font-size: 12px;
}

.ysps-1-1-2-2>form>input {
    color: #2C72A1;
    border: none;
    outline: none;
    width: 90%;
    padding: 12px 24px;
    margin: 36px 0px 0px 0px;
    border-radius: 50px;
}

.btn-2 {
    background-color: #2C72A1;
    border: none;
    outline: none;
    padding: 8px 16px;
    border-radius: 16px;
    color: white;
    letter-spacing: 1px;
    font-weight: bold;
    margin: 16px 0px;
}

.delete {
    position: absolute;
    right: 16px;
}


.ysps-1-2 {
    width: 900px;
    margin: 60px auto;
}

.ysps-1-2>a {
    text-decoration: none;
    color: white;
    display: flex; 
    align-items: center;
    border-bottom: 1px solid #2C72A1;
    padding: 0px 0px 10px  0px;
    margin: 0px 0px 20px 0px;
}

.ysps-1-2>a>h2 {
    color: #2C72A1;
    margin: 0px;
}

.ysps-1-2>a>img {
    border-radius: 50%;
    height: 50px;
    width: 50px;
    margin: 0px 20px 0px 0px;
    object-fit: cover;
}

.ysps-1-2>h1 {
    font-size: 30px;
    text-transform: uppercase;
}



@media only screen and (max-width:1000px) {
    .ysps-1 {
        padding: 128px 0px;
    }


    .ysps-1-1 {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }


    .ysps-1-2 {
        width: 90%;
        margin: 60px auto;
    }


    .ysps-1-1>img {
        border-radius: 16px;
        width: 400px;
        margin: 0px 0px 32px 0px;
    }

    .ysps-1-1-2>div:nth-child(1) {
        height: auto;
        max-height: 460px;
        overflow-y: scroll;
    }
}

@media only screen and (max-width:600px) {
    .ysps-1-1-2 {
        border: none;
        padding: 16px;
        width: 90%;
    }

}

@media only screen and (max-width:450px) {
    .ysps-1-1>img {
        border-radius: 16px;
        width: 300px;
        margin: 0px 0px 0px 0px;
        height: auto;
    }

    .ysps-1-1-2-2>input {
        outline: none;
        width: 86%;
        padding: 12px 24px;
        margin: 36px 0px 0px 0px;
        border-radius: 50px;
    }

}