.ycs-1{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 128px 0px;
}

.ycs-1-1{
    width: 600px;
    /* background-color: #2C72A1; */
    border: 1px solid rgba(255, 255, 255, 0.312);
    border-radius: 16px;
    padding: 32px;
    display: flex;
    flex-direction: column;
    
}

.ycs-1-1>input{
    background-color: rgb(255, 255, 255);
    color: #2C72A1;
    border: none;
    outline: none;
    padding: 12px 24px;
    margin: 12px 0px;
    border-radius: 9px;

    border: 1px solid rgba(255, 255, 255, 0.312);
    outline: none;
}

.ycs-1-1>textarea{
    background-color: rgb(255, 255, 255);
    color: #2C72A1;
    border: none;
    outline: none;
    padding: 12px 24px;
    margin: 12px 0px;
    border-radius: 8px;
    font-family: Arial, Helvetica, sans-serif;
    resize: vertical;
}



.imaps-1-1 {
    width: 360px;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 9px ;
    padding: 30px;
    border: 1px solid #EAECF0;
    color: #1B262C;
}




.imaps-1-1>input::placeholder{
    font-size: 14px;
    /* color: #1B262C; */
}

.ycs-1-1>input[type="file"]{
    font-size: 14px;
    padding: 12px 16px;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgba(255, 255, 255, 0.312);
    outline: none;
    border-radius: 9px;
}

.ycs-1-1>input[type="file"]::file-selector-button{
    display: none;
} 